// Call plugin.
import pluginInterface from '../interfaceNew'
import * as sagas from './sagas'
import events from '../interfaceNew/events'

// Other plugins.
import { mapEvents } from '../../events/interface/actions'
import { update as updateConfig, setSdpHandlers } from '../../config/interface/actions'
import { logManager } from '../../logs'

// Helpers.
import { mergeValues, autoRestart } from '../../common/utils'

// Libraries.
import { values } from 'lodash/fp'
import { fork, put } from 'redux-saga/effects'
import { getWebRTCSupportCapabilities } from 'kandy-webrtc'

// Parse and/or Validate
import { validation as v8n, parse, enums } from '../../../src/common/validation'

const log = logManager.getLogger('CALL')

// eslint-disable-next-line no-warning-comments
/**
 * Configuration options for the call feature.
 * @public
 * @name config.call
 * @memberof config
 * @instance
 * @param {Object} call The call configuration object.
 * @param {string} [call.sdpSemantics='unified-plan'] The sdpSemantics to use (`'unified-plan'` or `'plan-b'`).
 *    As 'plan-b' has become a deprecated option, it will therefore be removed in the future.
 * @param {Array<call.IceServer>} [call.iceServers] The list of ICE servers to be used for calls.
 * @param {number} [call.iceCollectionDelay=1000] Time, in milliseconds, to delay in between
 *    ICE candidate checks. If ICE collection does not complete normally, the SDK will check
 *    collected candidates at this interval to determine if the operation can continue.
 * @param {number} [call.maxIceTimeout=3000] Maximum time, in milliseconds, to wait for ICE
 *    collection to complete normally. After this time, the process will timeout and the
 *    operation will attempt to continue no matter how many candidates have been collected.
 * @param {Function} [call.iceCollectionCheck] Function to check whether collected candidates
 *    can be used to continue the operation. The function will receive the following two parameters:
 *    an array of ICE servers & an extra details object, to help in the decision making.
 *    The details object includes: an array of ICE candidates, the elapsed time so far (in milliseconds),
 *    the maximum ICE collection timeout (in milliseconds) and an array of offered media.
 *    The function must return a boolean value of whether the SDK should attempt to continue
 *    the operation. By default, the check is to ensure at least one relay candidate has been
 *    collected.
 * @param {boolean} [call.serverTurnCredentials=true] Whether server-provided TURN credentials should be used.
 * @param {Array<call.SdpHandlerFunction>} [call.sdpHandlers] List of SDP handler functions to modify SDP. Advanced usage.
 * @param {boolean} [call.removeH264Codecs=true] Whether to remove "H264" codec lines from incoming and outgoing SDP messages.
 * @param {boolean} [call.earlyMedia=false] Whether early media should be supported for calls. Not supported on Firefox.
 * @param {boolean} [call.resyncOnConnect=false] Whether the SDK should re-sync all call states after connecting (requires Kandy Link 4.7.1+).
 * @param {boolean} [call.mediaBrokerOnly=false] Whether all Calls will be anchored on the MediaBroker instead of being peer-to-peer. Set to true if the backend is configured for broker only mode.
 * @param {boolean} [call.removeBundling=false] Whether to remove a=group attributes to stop media bundling from incoming and outgoing SDP messages.
 * @param {string} [call.ringingFeedbackMode='auto'] The mode for sending ringing feedback to the Caller ('auto', 'manual').
 *    By default, feedback will be automatically sent when a call has been received. In 'manual' mode, the application
 *    must initiate the feedback being sent. See the `call.sendRingingFeedback` API for more info.
 * @param {number} [call.callAuditTimer=25000] Time interval, in milliseconds between call audits.
 * @param {number} [call.mediaConnectionRetryDelay=3000] Delay, in milliseconds for the passive side of a call to wait before trying a media reconnection.
 * @param {boolean} [call.normalizeDestination=true] Specifies whether or not SIP address normalization will be applied.
 */

/**
 * @private
 * @name config.call
 * @memberof config
 * @instance
 * @param {string} [trickleIceMode='NONE'] The Trickle ICE method to use for calls. Currently, no mode is supported.
 */
const defaultOptions = {
  sdpSemantics: 'unified-plan',
  iceServers: [],
  iceCollectionDelay: 1000,
  maxIceTimeout: 3000,
  // Defaults set by the Webrtc stack:
  //    iceCollectionCheck: Has at least one relay candidate.
  serverTurnCredentials: true,
  sdpHandlers: [],
  removeH264Codecs: true,
  earlyMedia: false,
  resyncOnConnect: false,
  mediaBrokerOnly: false,
  removeBundling: false,
  trickleIceMode: 'NONE',
  normalizeDestination: true,
  ringingFeedbackMode: 'auto',
  callAuditTimer: 25000,
  mediaConnectionRetryDelay: 3000
}

// config validation
const v8nValidation = v8n.schema({
  sdpSemantics: enums(['unified-plan', 'plan-b']),
  iceServers: v8n.array(),
  iceCollectionDelay: v8n.positive(),
  maxIceTimeout: v8n.positive(),
  // Defaults set by the Webrtc stack:
  //    iceCollectionCheck: v8n.optional(v8n.function()),
  serverTurnCredentials: v8n.boolean(),
  sdpHandlers: v8n.array(),
  removeH264Codecs: v8n.boolean(),
  earlyMedia: v8n.boolean(),
  resyncOnConnect: v8n.boolean(),
  mediaBrokerOnly: v8n.boolean(),
  removeBundling: v8n.boolean(),
  trickleIceMode: v8n.string(),
  normalizeDestination: v8n.boolean(),
  ringingFeedbackMode: enums(['auto', 'manual']),
  callAuditTimer: v8n.positive(),
  mediaConnectionRetryDelay: v8n.positive()
})
const parseOptions = parse('call', v8nValidation)

export default function callsLink (options = {}) {
  const { mediaDevices, peerConnection } = getWebRTCSupportCapabilities()
  if (!mediaDevices || !peerConnection) {
    log.warn('Calls are not supported on this platform due to lack of WebRTC support. Call APIs will not be available.')
    return
  }

  // For backwards compatibility between old ICE config and new config.
  if (options.iceserver && !options.iceServers) {
    log.warn('Call configuration `iceserver` is being replaced by `iceServers`. Please update to the config.')
    options.iceServers = options.iceserver
    delete options.iceserver
  }

  if (options.ringingFeedbackMode && !['auto', 'manual'].includes(options.ringingFeedbackMode)) {
    log.warn('Call configuration `ringingFeedbackMode` invalid. Switching to `auto`.')
    options.ringingFeedbackMode = 'auto'
  }

  options = mergeValues(defaultOptions, options)
  parseOptions(options)

  function * init ({ webRTC }) {
    // Change sdpSemantics if not supported
    const isPlanB = options.sdpSemantics === 'plan-b'
    const browserDetails = webRTC.getBrowserDetails()

    // SDP Semantic 'Plan-B' is only supported on Chrome and only prior to M93.
    // If the configs say to use Plan-B, but the browser does not support it,
    //    change to Unified-Plan automatically to prevent potential issues.
    const noPlanBSupport =
      browserDetails.browser !== 'chrome' || (browserDetails.browser === 'chrome' && browserDetails.version >= 93)

    if (isPlanB && noPlanBSupport) {
      log.warn('Only Chrome prior to M93 supports `plan-b` sdpSemantics. Switching to `unified-plan`.')
      options.sdpSemantics = 'unified-plan'
    }

    /*
     * Firefox does not support pranswer, so it can't receive early media.
     * Ref: https://bugzilla.mozilla.org/show_bug.cgi?id=1004510
     */
    if (browserDetails.browser === 'firefox' && options.earlyMedia === true) {
      log.warn('Firefox does not support pranswer for Early Media. Disabling early media configuration.')
      options.earlyMedia = false
    }

    yield put(updateConfig(options, pluginInterface.name))
    yield put(mapEvents(events))

    // Update the SDP handlers in the config (user provided + some of our own)
    const sdpHandlerOptions = {
      removeH264Codecs: options.removeH264Codecs
    }
    yield put(setSdpHandlers(options.sdpHandlers, sdpHandlerOptions))

    // Dependencies to be provided to every call saga.
    const deps = {
      webRTC: webRTC.managers,
      browserDetails: webRTC.getBrowserDetails
    }

    // Wrap the call sagas in a function that provides them with the webRTC stack.
    const wrappedSagas = values(sagas).map(saga => {
      return autoRestart(() => saga(deps))
    })

    // Run all of the sagas.
    for (const saga of wrappedSagas) {
      yield fork(saga)
    }
  }

  return {
    name: pluginInterface.name,
    api: pluginInterface.api,
    reducer: pluginInterface.reducer,
    init,
    capabilities: ['call', 'link_call', 'link_user_id', 'link_pstn']
  }
}
